@use "../common/variables.scss";

.form-control {
  margin: 0;
  padding: 14px 12px 11px 15px;
  font-family: variables.$font-primary;
  font-size: 16px;
  line-height: 18px;
  color: variables.$text-secondary;
  text-transform: uppercase;
  border: 1px solid #e7e7e7;

  @media (min-width: variables.$tablet) {
    padding: 11px 20px;
    font-size: 20px;
    line-height: 20px;
  }
}
